<template>
  <div class="third">
      <div class="save" @click="save()">保存</div>
      <div class="tool">
        <div
          class="component"
          :class="type"  
          @mousedown="showDom($event,index)"
          v-for="(type,index) in types" 
          :key="index">
          <div></div>
        </div>
      </div>
      <div class="content">
        <div
        :style="{width:`${box.width}px`,height:`${box.height}px`,left:`${box.left}px`,top:`${box.top}px`}" 
        :class="types[box.type]" 
        v-for="(box,index) in boxList" 
        :key="index"
        v-if="box.width?true:false"
        @click.right="showDel($event,index)"
        @mousedown="openReset($event,index)" 
        @mouseup="closeReset($event)" 
        class="item">
          <div></div>
          <span class="resize" @mousedown="openResize($event,index)" @mouseup="closeResize()"></span>
        </div>
      </div>
      <div class="del" v-show="del" :style="{top:`${position.top}px`,left:`${position.left}px`}" @click="delBox()">删除</div>
      <div id="clone" 
        v-show="cloneDom"
        @mouseup="cloneDown($event)" 
        :class="types[active]"
        :style="{left:`${cloneX}px`,top:`${cloneY}px`}"
        ></div>
  </div>
</template>

<script>
export default {
  name: 'Third',
  data () {
    return {
      types:['triangle','round','react'],
      boxList:[
        {type:0,left:0,top:0,width:0,height:0}
      ],
      cloneDom:false,
      cloneX:null,
      cloneY:null,
      startX:null,
      startY:null,
      originX:null,
      originY:null,
      active:null,
      coping:false,
      dargging:false,
      moving:false,
      del:false,
      position: {
        top: 0,
        left: 0
      }
    }
  },
  mounted() {
    document.oncontextmenu = function(e) {
      e.preventDefault();
    };
    document.addEventListener('mousemove',this.adjust,false)
    document.addEventListener('mousemove',this.reset,false)
    document.addEventListener('mousemove',this.resize,false)
    this.isData()
  },
  methods:{
    showDom(event,index){
      this.active = index
      this.coping = true
      this.cloneDom = true
      this.cloneX = event.clientX-10
      this.cloneY = event.clientY-20      
    },
    adjust(event){
      if(this.coping){
        this.cloneX = event.clientX-10
        this.cloneY = event.clientY-20 
      }
    },
    cloneDown(event){
      this.cloneDom = false
      this.coping = false
      let offsetLeft=document.querySelector('.content').offsetLeft
      let offsetTop=document.querySelector('.content').offsetTop
      let left=event.currentTarget.offsetLeft
      let top=event.currentTarget.offsetTop
      let X=left-offsetLeft
      let Y=top-offsetTop
      if(X>0&&Y>0){
        this.boxList.push({type:this.active,left:this.cloneX-offsetLeft,top:this.cloneY-offsetTop,width:40,height:40})
      }    
    },
    resize(event){
      if(this.dargging){
        let offsetLeft=document.querySelector('.content').offsetLeft
        let offsetTop=document.querySelector('.content').offsetTop
        let width = event.clientX-this.boxList[this.active].left-offsetLeft+10
        let height = event.clientY-this.boxList[this.active].top-offsetTop
        if(!this.out){
          this.$set(this.boxList[this.active],'width',width)
          this.$set(this.boxList[this.active],'height',height)
        }else{
          this.out = false
        }        
      }    
    },
    openResize(event,index){
      event.stopPropagation()
      this.dargging = true
      this.active = index
    },
    closeResize(){
      this.dargging = false
      this.active = null
    },
    reset(event){
      if(this.moving){
        let X = this.originX+event.clientX-this.startX
        let Y = this.originY+event.clientY-this.startY
        this.$set(this.boxList[this.active],'left',X)
        this.$set(this.boxList[this.active],'top',Y)
      }
    },
    openReset(event,index){
      this.moving = true
      this.active = index
      this.startX = event.clientX
      this.startY = event.clientY
      this.originX = this.boxList[index].left
      this.originY = this.boxList[index].top
    },
    closeReset(event){
      this.moving = false
      this.startX = null
      this.startY = null
      this.active = null
    },
    showDel(event,index){
        this.del = !this.del
        this.active = index
        this.$set(this.position,'top',event.clientY)
        this.$set(this.position,'left',event.clientX)
    },
    delBox() {
      this.boxList.splice(this.active, 1)
      this.del = !this.del
      this.active = null
    },
    save(){
      let key = 'test'
      let data=JSON.stringify(this.boxList)
      localStorage.setItem(key, data)
      alert('保存成功了')
    },
    isData(){
      if(localStorage.getItem('test')){
        let data=JSON.parse(localStorage.getItem('test'))
        this.boxList = data
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.third{
  .del {
    width: 70px;
    height: 25px;
    line-height: 25px;
    background: rgb(240, 220, 46);
    position: absolute;
    cursor: pointer;
  }
  #clone{
    position:absolute;
    opacity: 0.85;
    z-index: 999;
    width: 40px;
    height: 40px;
    cursor: move;
  }
  .triangle{
      margin:10px auto;
      background: rgb(58, 118, 248);
      display: flex;
      justify-content: space-around;
      align-items: center;
      div{
        border-left: 15px solid  transparent;
        border-right: 15px solid transparent;
        border-bottom: 30px solid #fff;
      }
    };
    .round{
      margin:10px auto;
      background: rgb(58, 118, 248);
      display: flex;
      justify-content: space-around;
      align-items: center;
      div{
        width: 30px;
        height: 30px;
        background: #fff;
        border-radius: 50%; 
      }
    }
    .react{
      margin:10px auto;
      background: rgb(58, 118, 248);
      display: flex;
      justify-content: space-around;
      align-items: center;
      div{
        width: 30px;
        height: 30px;
        background: #fff;
      }
    }
  width: 100%;
  height: 100%;
  padding: 60px 30px 90px 90px;
  box-sizing: border-box;
  .save{
    position: fixed;
    top:10px;
    right: 30px;
    width: 55px;
    height: 35px;
    color: #fff;
    text-align: center;
    line-height: 35px;
    border-radius: 5px;
    background: rgb(58, 118, 248);
    cursor: default;
  }
  .tool{
    position:fixed;
    top:60px;
    left:10px;
    width: 60px;
    height: 500px;
    border: 1px solid rgb(58, 118, 248);
    .component{
      width: 40px;
      height: 40px;
    }
  }
  .content{
    border: 1px solid rgb(58, 118, 248);
    width: 100%;
    height: 100%;
    position: relative;
    .triangle{
      background: rgb(58, 118, 248);
      display: flex;
      justify-content: space-around;
      align-items: center;
      div{
        border-left: 15px solid  transparent;
        border-right: 15px solid transparent;
        border-bottom: 30px solid #fff;
      }
    };
    .round{
      background: rgb(58, 118, 248);
      display: flex;
      justify-content: space-around;
      align-items: center;
      div{
        width: 30px;
        height: 30px;
        background: #fff;
        border-radius: 50%; 
      }
    }
    .react{
      background: rgb(58, 118, 248);
      display: flex;
      justify-content: space-around;
      align-items: center;
      div{
        width: 30px;
        height: 30px;
        background: #fff;
      }
    }
    .item{
      position: absolute;
      .resize{
        display: block;
        position:absolute;
        width: 15px;
        height: 15px;
        bottom: 0;
        right: 0;
        cursor: se-resize;
      }
    }
  }
}
</style>



