<template>
    <div class="first">
        <div class="box" v-for="(box,index) in boxes" :key="index" :draggable="box.content?true:false" 
        @dragenter="dragEnter(index)" @dragstart="dragStart(index)" @drop="exchange($event)" @dragover="allowDrop($event)">
            <div class="plus" v-if="!box.content" @click="boxSwitch()"></div>
            <div class="content" v-else @click.right="showDel($event,index)">{{box.content}}</div>
        </div>
        <div class="add" v-show="add">
            <p class="message">新增</p>
            <div class="text">
                <textarea name="" id="" cols="30" rows="10" placeholder="请填写信息" v-model="message"></textarea>
            </div>
            <div class="button">
                <div class="cancel" @click="boxSwitch($event)">取消</div>
                <div class="confirm"  @click="addBox()">确定</div>
            </div>
        </div>
        <div class="del" v-show="del" :style="{top:`${position.top}px`,left:`${position.left}px`}" @click="delBox()">删除</div>
    </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      boxes: [{ content: "" }],
      message: "",
      add: false,
      del: false,
      position: {
        top: 0,
        left: 0
      },
      sign: null,
      origin: null,
      target: null
    };
  },
  mounted() {
    document.oncontextmenu = function(e) {
      e.preventDefault();
    };
  },
  methods: {
    boxSwitch() {
        this.add = !this.add;
    },
    showDel(event,index){
        this.del = !this.del
        this.sign = index
        this.$set(this.position,'top',event.pageY)
        this.$set(this.position,'left',event.pageX)
    },
    addBox() {
      this.boxes.unshift({ content: this.message })
      this.add = false
      this.message = ''
    },
    delBox() {
      this.boxes.splice(this.sign, 1)
      this.del = !this.del
    },
    dragStart(index){
      this.origin = index
    },
    dragEnter(index){
      this.target = index
    },
    exchange(event){
      event.preventDefault();
      if(this.target===this.boxes.length-1){
        return
      }
      let center = this.boxes[this.origin]
      this.boxes.splice(this.origin,1,this.boxes[this.target])
      this.boxes.splice(this.target,1,center)
    },
    allowDrop(event){
      event.preventDefault()
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.first {
  display: flex;
  width: 1200px;
  margin: 0 auto;
  flex-wrap: wrap;
  .box {
    border: 1px dashed rgb(116, 147, 248);
    display: flex;
    align-items: center;
    width: 250px;
    height: 150px;
    justify-content: space-around;
    margin:20px;
    cursor: pointer;
    .content{
        width: 100%;
        height: 100%;
    }
    .plus {
      position: relative;
      width: 10px;
      height: 10px;
      &::before {
        position: absolute;
        content: "";
        width: 70px;
        height: 10px;
        background: #333;
        left: -30px;
      }
      &::after {
        display: block;
        content: "";
        position: absolute;
        height: 70px;
        width: 10px;
        background: #333;
        top: -30px;
      }
    }
  }
  .add {
    width: 350px;
    height: 250px;
    border: 1px solid rgb(116, 147, 248);
    position:absolute;
    top:50%;
    left:50%;
    margin-top:-125px;
    margin-left:-175px;
    background:#fff;
    .message {
      background: rgb(116, 147, 248);
      color: #fff;
      line-height: 25px;
      height: 25px;
      margin: 0;
    }
    .text {
      height: 200px;
      padding: 15px;
      box-sizing: border-box;
      textarea {
        border-color: #ddd;
        border-radius: 5px;
        width: 100%;
        height: 100%;
        resize: none;
        outline: none;
      }
      &:focus {
        outline: none;
      }
    }
    .button {
      height: 25px;
      line-height: 25px;
      color: #fff;
      background-color: rgb(116, 147, 248);
      display: flex;
      .cancel,
      .confirm {
        flex: 1;
        text-align: center;
        cursor: pointer;
      }
    }
  }
  .del {
    width: 70px;
    height: 25px;
    line-height: 25px;
    background: rgb(240, 220, 46);
    position: absolute;
    cursor: pointer;
  }
}
</style>



