<template>
  <div class="menu">
    <router-link :to="menu.link" v-for="(menu,index) in menus" :key="index">{{ menu.name }}</router-link>    
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      menus:[
        {name:'案例一',link:'/'},
        {name:'案例二',link:'/second'},
        {name:'案例三',link:'/third'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.menu{
  position:fixed;
  left:50px;
  bottom:50px;
  z-index: 99999;  
}
a{
  color: #42b983;
  margin: 15px;
}
</style>



