<template>
  <div class="second" @click.right="showButton()">
    <div class="charts"></div>
    <div class="edit" v-show="edit" :style="{top:`${position.top}px`,left:`${position.left}px`}" @click="showEdit()">修改</div>
    <div class="box" v-show="box">
        <p class="message">图标编辑</p>
        <div class="title">
          <input type="text" v-model="option.title.text">
        </div>
        <span @click="addData(1)">+</span><span @click="addData(2)">-</span>
        <table width="100">
          <tr>
            <th width="20%">选择</th>
            <th width="40%">选项名</th>
            <th width="40%">值</th>
          </tr>
          <tr v-for="(item,index) in option.series[0].data" :key="index">
            <td width="20%"><input type="checkbox" name="" id="" @click="choose($event,index)"></td>
            <td width="40%">{{item.name}}</td>
            <td width="40%">{{item.value}}</td>
          </tr>
          <tr v-show="add" class="add-data">
            <td width="20%"><input type="checkbox" name="" id="" @click="choose($event,option.series[0].data.length)"></td>
            <td width="40%"><input type="text" v-model="addName" class="add-text"></td>
            <td width="40%"><input type="text" v-model="addValue" class="add-text"></td>
          </tr>
        </table>
        <div class="button">
            <div class="cancel" @click="cancelData()">取消</div>
            <div class="confirm"  @click="changeData()">确定</div>
        </div>
    </div>
  </div>
</template>

<script>
var echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/pie");
require('echarts/lib/component/legend');
require('echarts/lib/component/title');

export default {
  name: "HelloWorld",
  data() {
    return {
      position: {
        top: 0,
        left: 0
      },
      add:false,
      addName:'',
      addValue:'',
      chooseIndex:[],
      edit:false,
      box:false,
      option: {
        title:{
          show:true,
          text:'某站点用户访问数据来源',
          textStyle:{
            color:'#333',
          },
          x:'center'
        },
        legend: {
          orient: "vertical",
          x: "left",
          data: ["直接访问", "邮件营销", "联盟广告", "视频广告", "搜索引擎"]
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: true,
                position: "outline"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: true
              }
            },
            data: [
              { value: 335, name: "直接访问" },
              { value: 310, name: "邮件营销" },
              { value: 234, name: "联盟广告" },
              { value: 135, name: "视频广告" },
              { value: 1548, name: "搜索引擎" }
            ]
          }
        ]
      }
    };
  },
  mounted() { 
    this.drawLine()
    document.oncontextmenu = function(e) {
      e.preventDefault();
    };
    document.addEventListener('click',()=>{
      this.edit = false
    });   
  },
  methods: {
    drawLine(){ 
      var myChart = echarts.init(document.querySelector(".charts"));   
      myChart.setOption(this.option);
      window.onresize=myChart.resize;
    },
    showButton(){
      this.edit = !this.edit
      this.$set(this.position,'top',event.pageY)
      this.$set(this.position,'left',event.pageX)
    },
    showEdit(event,index){
        this.box = !this.box
    },
    choose(event,index){
      if(event.target.checked===true){
        this.chooseIndex.push(index)
      }else{
        let num=this.chooseIndex.indexOf(index)
        this.chooseIndex.splice(num,1)
      }    
    },
    changeData(){
      this.box=!this.box
      this.option.series[0].data.push({ value: this.addValue, name: this.addName })
      this.option.legend.data.push(this.addName)
      this.addName=''
      this.addValue=''
      this.add=false
      var myChart = echarts.init(document.querySelector(".charts"))
      myChart.clear()
      myChart.setOption(this.option);
    },
    addData(value){
      value===1?this.add=true:this.add=false
    },
    cancelData(){
      this.box=false
    }
  },
  watch:{
    
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.second {
  width: 100%;
  height: 100%;
  .charts{
    width: 100%;
    height: 100%;
  }
  .edit {
    width: 70px;
    height: 25px;
    line-height: 25px;
    background: rgb(240, 220, 46);
    position: absolute;
    cursor: pointer;
  }
  .box {
    width: 350px;
    border: 1px solid rgb(116, 147, 248);
    position:absolute;
    top:50%;
    left:50%;
    margin-top:-125px;
    margin-left:-175px;
    background:#fff;
    .message {
      background: rgb(116, 147, 248);
      color: #fff;
      line-height: 25px;
      height: 25px;
      margin: 0;
    }
    span{
      color:rgb(116, 147, 248);
      font-size: 30px;
      margin: 0 30px;
      cursor: pointer;
    }
    table{
      width: 350px;
      .add-data .add-text{
        width: 120px;
      }
    }
    .text {
      height: 200px;
      padding: 15px;
      box-sizing: border-box;
      textarea {
        border-color: #ddd;
        border-radius: 5px;
        width: 100%;
        height: 100%;
        resize: none;
        outline: none;
      }
      &:focus {
        outline: none;
      }
    }
    .button {
      height: 25px;
      line-height: 25px;
      color: #fff;
      background-color: rgb(116, 147, 248);
      display: flex;
      .cancel,
      .confirm {
        flex: 1;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}
</style>



