import Vue from 'vue'
import Router from 'vue-router'
import First from '@/pages/First'
import Second from '@/pages/Second'
import Third from '@/pages/Third'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'First',
      component: First
    },
    {
      path: '/second',
      name: 'Second',
      component: Second
    },
    {
      path: '/third',
      name: 'Third',
      component: Third
    }
  ]
})



